<template>
  <div class="wrapper">
    <tabel class="tabel" :key="updateKey">
      <thead>
        <tr class="bar_row">
          <th class="bar_item item" v-for="param in params" :key="param">
            <div class="bar_item_group">
              <div>{{ param }}</div>
              <button v-show="param" class="bar_item_icon"></button>
            </div>
          </th>
        </tr>
        <tr>
          <th
            class="item"
            v-show="show_filter"
            v-for="(filter, idx) in params.length"
            :key="idx"
          >
            <div class="filter">
              <button @click="changeComparison(idx)">
                <div
                  class="icon"
                  :class="{
                    tilde: comparisons[idx] == 1,
                    equal: comparisons[idx] == 2,
                    more: comparisons[idx] == 3,
                    more_or_equal: comparisons[idx] == 4,
                    less: comparisons[idx] == 5,
                    less_or_equal: comparisons[idx] == 6,
                  }"
                ></div>
              </button>
              <input v-model="filterValue[idx]" type="text" />
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr class="row" v-for="(row, i) in paginatedData" :key="row">
          <td class="item">
            <input
              type="checkbox"
              class="checkbox"
              :id="i"
              v-model="changeValue[data.indexOf(row)]"
            />
            <label :for="i"></label>
          </td>
          <td class="item" v-for="(item, idx) in row" :key="idx">{{ item }}</td>
          <td class="item"><div class="edit_icon"></div></td>
        </tr>
      </tbody>
    </tabel>
    <div class="bottom">
      <button v-if="page > 1" @click="page -= 1">
        {{ "<" }}
      </button>
      <span style="margin: 5px">{{ page }}</span>
      <button v-if="page * count < data.length" @click="page += 1">
        {{ ">" }}
      </button>
      <input type="number" v-model="count" class="count" @input="page = 1" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Main_grid",
  props: {
    params: [],
    data: [],
    show_filter: Boolean,
  },
  data() {
    return {
      coll: [],
      count: 3,
      page: 1,
      filterValue: [],
      updateKey: 0,
      comparisons: [],
      changeValue: [],
    };
  },
  watch: {
    count() {
      if (this.count < 0) {
        this.count = 0;
      }
    },
    paginatedData: {
      handler: function () {
        this.updateKey += 1;
        this.changeValue = [];
      },
      deep: true,
    },
    comparisons: {
      handler: function () {
        this.comparisons.forEach((val, i) => {
          val > 6 ? (this.comparisons[i] = 1) : val;
        });
      },
      deep: true,
    },
    data: {
      handler: function () {
        this.comparisons = [];
        this.fillColls();
        this.filterValue = [];
        this.page = 1;
      },
      deep: true,
    },
    show_buttons: function () {
      this.$emit("show_buttons1", this.show_buttons);
    },
  },
  computed: {
    show_buttons() {
      let value = false;
      this.changeValue.forEach((val) => {
        if (val != undefined) {
          value = val || value;
        }
      });
      return value;
    },
    paginatedData() {
      return this.data.slice(
        this.page * this.count - this.count,
        this.page * this.count
      );
    },
  },
  mounted() {
    this.fillColls();
  },
  methods: {
    fillColls() {
      this.coll = Object.keys(this.params);
      this.coll.forEach(() => {
        this.comparisons.push(1);
      });
    },
    changeComparison(idx) {
      this.comparisons[idx] = this.comparisons[idx] + 1;
    },
  },
};
</script>

